.content {
    width: 60%;
    height: 100%;
    background-color: #e1e8ee;
    border-radius: 20px;
    padding: 15px;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgb(40 4 44);
  }
  .content h2 {
    font-size: 16px;
    text-align: center;
    color: #666;
  }
  .content p {
    word-break: break-all;
    text-indent: 1.5em;
    line-height: 1.5;
    font-size: 16px;
    color: #888;
  }
  .content button {
    float: right;
    border-radius: 8px;
    border: 0;
    width: 65px;
    height: 25px;
    background-color: #6b92a4;
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    font-weight: bold;
  }
  .content button:hover {
    transition: 0.3s;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.6);
  }
  .cover {
    width: 40%;
    height: 100%;
    border-radius: 20px;
    background-color: rgb(97, 4, 92);
    box-shadow: 0 0 10px rgb(71, 1, 71);
  }
  .cover .author-wrapper {
    width: 67%;
    margin: 0 auto;
    cursor: pointer;
  }
  .cover .author-wrapper img,
  .cover .author-wrapper span {
    display: block;
    float: left;
  }
  .cover .author-wrapper img {
    width: 30px;
    box-shadow: 0 0 10px rgb(255, 255, 255);

  }
  .cover .author-wrapper span {
    margin-left: 10px;
    font-size: 16px;
    font-weight: bolder;
    color: #666;
    line-height: 30px;
  }
  .cover .img-wrapper {
    width: 100%;
    height: 80%;
    position: relative;
  }
  .cover .img-wrapper img {
    width: 67%;
    height: 80%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 10px rgb(255, 255, 255);
  }
  .book-detail {
    display: flex;
    width: 600px;
    height: 350px;
    min-width: 600px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  